// app/products/[slug]/landing-page/page.tsx

import LandingPageBuilder from "@/app/landing-pages/new/page";
import axiosClient from "@/lib/axiosClient";
import { notFound } from "next/navigation";

export default async function ProductLandingPageBuilder({
  params,
}: {
  params: { slug: string };
}) {
  let productId: number | null = null;

  try {
    // Adjust endpoint to match your API
    const res = await axiosClient.get(`/products?slug=${params.slug}`);
    if (res.data.success && res.data.data?.length > 0) {
      productId = res.data.data[0].id;
    }
  } catch (error) {
    console.error("Failed to fetch product by slug");
  }

  if (!productId) {
    notFound(); // Shows nice 404 page
  }

  return <LandingPageBuilder productId={productId} />;
}